<template>
  <div
    v-if="shopsStore.loading"
    class="d-flex align-center justify-center pa-6"
  >
    <v-progress-circular indeterminate color="primary" />
  </div>

  <template v-else>
    <v-row v-if="shopsStore.shops && shopsStore.shops.length">
      <v-col
        v-for="shop in shopsStore.shops"
        :key="shop.id"
        cols="12"
      >
        <shop-card
          :shop="shop"
          @click="openDetailsDialog(shop)"
        />
      </v-col>
    </v-row>

    <div v-else>No stores found.</div>
  </template>
</template>

<script setup>
import { onMounted } from "vue";

import { useMapStore } from "@/stores/map";
import { useShopsStore } from "@/stores/shopsStore";

import ShopCard from "./ShopCard.vue";

const mapStore = useMapStore();

const shopsStore = useShopsStore();

function openDetailsDialog({ id }) {
  mapStore.setSelectedItem({
    id,
    type: "shop",
  });
}

onMounted(() => {
  shopsStore.fetchShops();
});
</script>
