<template>
  <v-card class="content-card">
    <v-tooltip :text="$t('stores.addToFavorites')">
      <template v-slot:activator="{ props }">
        <v-btn
          v-bind="props"
          :icon="isFavorite ? 'mdi-star' : 'mdi-star-outline'"
          :color="isFavorite ? 'primary' : 'grey'"
          variant="text"
          size="small"
          class="add-to-favorite-button"
          @click.stop="addToFavorite"
        />
      </template>
    </v-tooltip>

    <v-row no-gutters>
      <v-col cols="4">
        <div class="shop-card-image">
          <img :src="shop.image" alt="">
        </div>
      </v-col>

      <v-col cols="8">
        <v-card-title class="pr-6">{{ shop.name }}</v-card-title>
        <div class="text-body-2 pl-4 pr-2">
          {{ shop.address.line1 }}, {{ shop.address.line2 }}
        </div>
      </v-col>
    </v-row>

    <v-divider />

    <v-card-text>
      <div class="truncate-text-4">
        {{ shop.description }}
      </div>
    </v-card-text>
  </v-card>
</template>

<script>
import { computed } from "vue";

import { useUserStore } from "@/stores/userStore";

export default {
  name: "StoreCard",
  props: {
    shop: {
      type: Object,
      required: true,
    },
  },
  setup(props) {
    const userStore = useUserStore();

    const addToFavorite = () => {
      userStore.toggleFavoriteShop(props.shop.id);
    };

    const isFavorite = computed(() => {
      return userStore.isFavoriteShop(props.shop.id);
    });

    return {
      isFavorite,
      addToFavorite,
    };
  },
};
</script>

<style lang="scss">
.add-to-favorite-button {
  position: absolute;
  top: 4px;
  right: 4px;
}

.shop-card-image {
  height: 100px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px;

  img {
    display: block;
    object-fit: cover;
    max-height: 100%;
    max-width: 100%;
  }
}
</style>
