<template>
  <v-tabs
    v-model="activeTab"
    color="primary"
    grow
    class="mb-4 elevation-1"
    bg-color="inactive"
  >
    <v-tab v-for="tab in navigationTabs" :key="tab.value" :value="tab.value">
      {{ $t(tab.title) }}
    </v-tab>
  </v-tabs>

  <v-window v-model="activeTab">
    <v-window-item
      v-for="tab in navigationTabs"
      :key="tab.value"
      :value="tab.value"
    >
      <component :is="tab.value" />
    </v-window-item>
  </v-window>
</template>

<script>
import { ref } from "vue";

import ProductsList from "@/components/products/ProductsList.vue";
import PostsFeed from "@/components/posts/PostsFeed.vue";
import ShopsList from "@/components/shops/ShopsList.vue";

export default {
  name: "SidebarTabs",
  components: {
    ShopsList,
    ProductsList,
    PostsFeed,
  },
  setup() {
    const activeTab = ref("stores");

    const navigationTabs = [
      {
        title: 'stores.stores',
        value: "ShopsList",
      },
      {
        title: 'products.products',
        value: "ProductsList",
      },
      {
        title: 'posts.posts',
        value: "PostsFeed",
      },
    ];

    return {
      activeTab,
      navigationTabs,
    };
  },
};
</script>
