<template>
  <v-card v-if="product">
    <v-card-title>
      {{ product.title }}
    </v-card-title>
    <v-card-subtitle class="mt-3">
      <router-link :to="'/shops/' + product.store.id" class="text-secondary">
        {{ product.store.name }}
      </router-link>
    </v-card-subtitle>
    <v-card-text>
      <v-img :src="product.image" aspect-ratio="1.8" cover class="mb-2" />
      <div>
        <v-chip color="green" text-color="white" label>
          ${{ product.price }}
        </v-chip>
        <span class="opacity-30"> / {{ product.unit.name }}</span>
      </div>
      <div class="mb-4 mt-4">
        {{ product.description }}
      </div>

      <div class="mt-4">
        <add-to-basket-button :product="product" variant="outlined" />
      </div>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { computed, defineProps } from "vue";
import { useProductsStore } from "@/stores/productsStore";

import AddToBasketButton from "@/components/products/AddToBasketButton.vue";

const props = defineProps({
  id: [String, Number],
});

const productsStore = useProductsStore();

const product = computed(() => productsStore.getById(props.id));
</script>
