<template>
  <div class="map-container d-flex align-center justify-center">
    <GoogleMap
      :api-key="apiKey"
      mapId="appMap"
      class="app-map"
      :center="mapCenter"
      :zoom="15"
      :map-type-control="false"
      :streetViewControl="false"
    >
      <AdvancedMarker
        v-for="marker in shopMarkers"
        :key="marker.id"
        :options="marker"
        :pinOptions="{ background: '#228B22', glyphColor: 'white' }"
        @click="showShopDetails(marker)"
      />
    </GoogleMap>

    <map-details-dialog />
  </div>
</template>

<script>
import { computed } from "vue";

import { useShopsStore } from "@/stores/shopsStore";

import { useMapStore } from "@/stores/map";

import { GoogleMap, AdvancedMarker } from "vue3-google-map";

import MapDetailsDialog from "@/components/home/map/MapDetailsDialog.vue";

export default {
  name: "MapMain",
  components: { GoogleMap, AdvancedMarker, MapDetailsDialog },
  setup() {
    const mapStore = useMapStore();

    const apiKey = process.env.VUE_APP_GOOGLE_MAPS_API_KEY;

    const mapCenter = {
      lat: 52.209427,
      lng: 21.070871,
    };

    const shopMarkers = computed(() => {
      return (useShopsStore().shops || []).map((shop) => {
        const { id, address, name } = shop;
        const { latitude, longitude } = address?.location || {};

        return {
          id,
          title: name,
          position: {
            lat: latitude,
            lng: longitude,
          },
        };
      });
    });

    const showShopDetails = (shop) => {
      mapStore.setSelectedItem({
        id: shop.id,
        type: "shop",
      });
    };

    return {
      apiKey,
      mapCenter,
      shopMarkers,
      showShopDetails,
    };
  },
};
</script>

<style>
.app-map {
  height: 100%;
  width: 100%;
}

.map-container {
  flex: 1;
  position: relative;
}
</style>
